import React from 'react';
import {
    SOURCE_MARKER,
    FEEDBACK_MARKER,
    SOURCE_HOME
} from '../../home/Constants'
import Popup from 'reactjs-popup';
import FeedbackForm from '../../home/FeedbackForm';


const FeedbackFormCode = "xpzelbad"

class CustomBotMessage extends React.Component {

    constructor(props) {
        super(props);
        if (this.props.message.includes(FEEDBACK_MARKER)) {
            [this.feedback, this.content] = this.props.message.split(FEEDBACK_MARKER, 2)
        } else {
            [this.content, this.source] = this.props.message.split(SOURCE_MARKER, 2)
        }
        this.state = { loading: true };
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ loading: false });
        }, this.props.loadTime);
    }

    getMessage() {
        return "Question " + this.feedback +  " doesn't look right, please review for correctness. The correct answer should be..."
    }

    getResponse() {
        return (<p>Thank you for your feedback!<br/>The question will be reviewed.</p>)
    }

    render() {
        return <div className="react-chatbot-kit-chat-bot-message">
                {this.state.loading ? this.props.loader : <span>{this.content} 
                    {this.source ? <i style={{color: '#606060', fontSize: '.8rem'}}>(Source:<a style={{color: 'blue'}} target="_blank" href={SOURCE_HOME[this.source]}>{this.source}</a>)</i> : ''}
                    {this.feedback ? <Popup trigger={<i style={{fontSize: '.8rem'}}><a style={{color: 'blue'}}> Feedback</a></i>} modal nested>
                        {close => (<FeedbackForm theme={this.props.theme} closePopup={close} message={this.getMessage()} response={this.getResponse()} title='Feedback' formCode={FeedbackFormCode}/>)}
                    </Popup> : ''}
                </span> }
            </div>
    }
}


export default CustomBotMessage;