import SaraChan from './bot/SaraChan'
import SocialNetWorkSharing from './home/SocialNetworkSharing'
import Carousel from './home/Carousel'
import {useState} from 'react'
import './App.css';
import {BOT_NAME, URL} from './home/Constants'
import CryptoJS from 'crypto-js';

function App() {

  const [page, setPage] = useState('home');

  const THEMES = [
    "theme-green",
    "theme-blue",
    "theme-orange",
    "theme-dark-blue",
    "theme-dark-green",
    "theme-dark-red",
  ]

  const chatBgCount = 7

  const [theme, setTheme] = useState(Math.floor(Math.random() * THEMES.length))
  const [chatBg, setchatBg] = useState(Math.floor(Math.random() * chatBgCount))

  function nextTheme() {
    setTheme((parseInt(theme) + 1) % THEMES.length)
  }
  
  return (
    <div>
      <div className={"main " + THEMES[parseInt(theme)]}>
        <header className="menu-bar">
            <div className="name" onClick={()=> window.location.href=URL} >
                <img alt="" className="icon-img" src="img/sarachan-icon.png"/>
              <div className="name-text">
                {BOT_NAME}
              </div>
            </div>
            <div className="nav">
              <div className={"nav-button" + (page === 'home' ? "-active" : "")} onClick={() => setPage('home')}>HOME</div>
              <div className={"nav-button" + (page === 'chat' ? "-active" : "")} onClick={() => setPage('chat')}>CHAT</div>
            </div>
        </header>
        <div className={"home" + (page === 'chat' ? "-hide" : "") }>
          <div className="home-content">
            <div className="content-top">
              <div className="arrow-box">
                <img alt="" className="arrow-img" src="img/getstarted-arrow.png" onClick={() => nextTheme()}/>
              </div>
              <h1 className="title">Practice makes perfect</h1>
              <p className="description">And {BOT_NAME} is here to help, so let's reinforce your knowledge!</p>
            </div>
            <Carousel theme={THEMES[parseInt(theme)]}/>
            <SocialNetWorkSharing />
          </div>
        </div>
        <div className={"chat" + (page === 'home' ? "-hide" : "")}>
          <SaraChan theme={THEMES[parseInt(theme)]} chatBg={parseInt(chatBg)}/>
        </div>
      </div >
      <div className="rotate-device">
        <div className="rotate-phone">
        </div>
        <div className="rotate-message">
          Please rotate your device for the best viewing experience!
        </div>
      </div>
    </div>
  );
}

export default App;
