import React from "react";
import './ControlBox.css'

const PLAY_IMG_SRC = "img/play-icon.png"
const PAUSE_IMG_SRC = "img/pause-icon.png"
const CHECK_IMG_SRC = "img/check-icon.png"

class ControlBox extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            timer : 0,
            imgSrc : PAUSE_IMG_SRC
        }
        this.switchTimerState = this.switchTimerState.bind(this)
        this.getTimerState = this.getTimerState.bind(this)
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(),
            1000 // tick every second
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        if (this.getTimerState() === 'timing') {
            this.setState((state) => ({
                timer: state.timer + 1,
                imgSrc: PAUSE_IMG_SRC
            }));
        } else if (this.getTimerState() === 'reset') {
            this.setState({
                timer: 0,
                imgSrc: PAUSE_IMG_SRC,
            });
        } else {
            this.setState({
                imgSrc: PLAY_IMG_SRC,
            });
        }
    }

    switchTimerState() {
        if (this.getTimerState() === 'timing') {
            this.props.setControlState(-1, -1, 'paused')
            this.setState({
                imgSrc: PLAY_IMG_SRC,
            })
        } else if (this.getTimerState() === 'paused') {
            this.props.setControlState(-1, -1, 'timing')
            this.setState({
                imgSrc: PAUSE_IMG_SRC
            })
        }
    }

    getTimerState() {
        return this.props.controlStateRef.timerState
    }

    getCorrectCount() {
        return this.props.controlStateRef.correctCount
    }

    getTotalCount() {
        return this.props.controlStateRef.totalCount
    }

    getTimer() {
        const pad = (n) => n < 10 ? `0${n}` : n;
        const h = Math.floor(this.state.timer / 3600);
        const m = Math.floor(this.state.timer / 60) - (h * 60);
        const s = Math.floor(this.state.timer - h * 3600 - m * 60);
        return `${pad(h)}:${pad(m)}:${pad(s)}`;
    }

    getScore() {
        return `${this.getCorrectCount()}/${this.getTotalCount()}`
    }

    getPercentage() {
        var score = (this.getCorrectCount() * 100) / this.getTotalCount()
        score = parseFloat(score).toFixed(1)
        return `${score}%`
    }

    render() {
        return (
            <div className="control-container">
                <div className={"control-box" + (this.getTotalCount() ?  '' :  '-hide')}>
                    <div className={"timing-box-" + this.getTimerState()}>
                        <img alt="" className="timer-img"  src={this.state.imgSrc} onClick={this.switchTimerState}/>
                        <div className="timer-box">
                            {this.getTimer()}
                        </div>
                    </div>
                    <div className="score-box">
                        <img alt="" className="score-img"  src={CHECK_IMG_SRC}/>
                        <div className="percent-box">
                            {this.getScore()} ({this.getPercentage()})
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ControlBox