import React, {useState} from "react";
import "./MultipleChoice.css";

const MultipleChoice = (props) => {

  const [disabled, setDisabled] = useState(false)

  const choice = props.options ? props.options : props.dynamicOptions.map(dynamicOption => dynamicOption())

  const options = choice.map((choice) => {
    return {
      text: choice,
      handler: () => { 
        if (!disabled) {
          setDisabled(true)
          props.actionProvider.createUserReply(choice) 
        }
      },
      id: "option-" + choice
    }
  }).map((option) => (
    <button className="multiple-choice-option" disabled={disabled} key={option.id} onClick={option.handler}>
      {option.text}
    </button>
  ));

  return <div className="multiple-choice-container">{options}</div>;
};

export default MultipleChoice;