import { createChatBotMessage } from 'react-chatbot-kit';
import MultipleChoice from "./components/MultipleChoice";
import {
  getGreetingMsg,
  getFirstTrainingMsg,
  getReadyOptionMsg,
  getStopOptionMsg
} from "./Message";
import {
  BOT_NAME, 
  TRAINING,
  MULTIPLE_CHOICE, 
  CUSTOM_MESSAGE
} from '../home/Constants'
import CustomBotMessage from './components/CustomBotMessage'

export const initializeMessage = () => {
  return [
    createChatBotMessage(getGreetingMsg(BOT_NAME), CUSTOM_MESSAGE()),
    createChatBotMessage(getFirstTrainingMsg(), CUSTOM_MESSAGE("trainingSelection")),
  ]
}

const config = {
  botName: BOT_NAME,
  initialMessages: initializeMessage(),
  customComponents: {
    botAvatar: () => <div className="empty-avatar"/>,
    userAvatar: () => <div className="empty-avatar"/>,
    botChatMessage: (props) => <CustomBotMessage {...props} loadTime={750}/>
  },
  customStyles: {
    botMessageBox: {
      backgroundColor: "#376B7E",
    },
    chatButton: {
      backgroundColor: "#5ccc9d",
    },
  },
  state: {
    training: '',
    qQueue: [],
    qCurrent: '',
    controlStateRef: '',
    setControlState: '',
  },
  widgets: [
    {
      widgetName: 'trainingSelection',
      widgetFunc: (props) => <MultipleChoice {...props} />,
      props: {
        options: TRAINING
      }
    },
    {
      widgetName: 'multipleChoice',
      widgetFunc: (props) => <MultipleChoice {...props} />,
      props: {
        options: MULTIPLE_CHOICE
      }
    },
    {
      widgetName: 'ready',
      widgetFunc: (props) => <MultipleChoice {...props} />,
      props: {
        dynamicOptions: [getReadyOptionMsg, getStopOptionMsg]
      }
    }
  ],
}

export default config