import React from "react";
import Chatbot from "react-chatbot-kit";
import config from "./ChatbotConfig";
import {initializeMessage} from "./ChatbotConfig";
import MessageParser from "./MessageParser";
import ActionProvider from "./ActionProvider";
import ControlBox from "./ControlBox";
import './SaraChan.css';
import {BOT_NAME, URL} from '../home/Constants';
import CustomBotMessage from './components/CustomBotMessage'

class SaraChan extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            correctCount: 0,
            totalCount: 0,
            timerState: 'stopped',
        };
        this.setControlState = this.setControlState.bind(this)
        this.getControlState = this.getControlState.bind(this)
        this.config = config
        this.config.state.controlStateRef = this.getControlState
        this.config.state.setControlState = this.setControlState
        this.config.customComponents.botChatMessage = (props) => <CustomBotMessage {...props} theme={this.props.theme} loadTime={750}/>
        this.resetKey = 0
    }

    getThemeClass() {
        return this.props.theme + '-chatbot'
    }

    getBgClass() {
        return 'chat-bg-' + this.props.chatBg
    }

    getControlState() {
        return this.state
    }

    reloadChat() {
        this.resetKey++
        this.config.message = []
        this.config.initialMessages = initializeMessage()
        this.config.state = {
            training: '',
            qQueue: [],
            qCurrent: '',
            controlStateRef: this.getControlState,
            setControlState: this.setControlState,
        }
        this.setState({
            correctCount: 0,
            totalCount: 0,
            timerState: 'stopped',
        });
    }

    setControlState(correctCount, totalCount, timerState) {
        if (timerState) {
            this.setState({
                timerState: timerState
            })
        }
        if (correctCount > -1) {
            this.setState({
                correctCount: correctCount
            });
        }
        if (totalCount > -1) {
            this.setState({
                totalCount: totalCount
            });
        }
    }

    render() {
        return <div className={"sara-chan " + this.getThemeClass() + " " + this.getBgClass()}>
            <div className="chatbot-header">
                <div className="chatbot-name" onClick={()=> window.location.href=URL}>
                    <img alt="" className="icon-img" src="img/sarachan-icon.png" />
                    <div className="name-text">
                        {BOT_NAME}
                    </div>
                </div>
                <div className="chatbot-reload-button" onClick={() => this.reloadChat()} >
                    <img alt="" className="reload-img" src="img/reload-icon.png" />
                </div>
            </div>
            <Chatbot
                config={this.config}
                actionProvider={ActionProvider}
                messageParser={MessageParser}
                key={this.resetKey} />
            <ControlBox
                controlStateRef={this.state}
                setControlState={this.setControlState} />
        </div>;
    };
}

export default SaraChan