import React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { useForm, ValidationError } from '@formspree/react';

const style = {
  position: 'absolute',
  textAlign: 'center',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '20rem',
  bgcolor: 'background.paper',
  border: 'none',
  borderRadius: '1rem',
  boxShadow: 24,
  p: 2,
  outline: 'none',
};
const btnStyle = {
  marginTop: '1rem',
  color: 'white',
  fontWeight: 'bold',
  borderRadius: '1rem',
  fontSize: '1rem',
  padding: '.5rem 1rem;',
  marginLeft: '1rem',
  marginRight: '1rem',
}

function FeedbackForm(props) {
  const [state, handleSubmit] = useForm(props.formCode);
  if (state.succeeded) {
    return (
      <Box sx={style}>
        <Typography component={'span'} id="modal-modal-title" sx={{ mt: 2, mb: 2 }}>
        {props.response}
        </Typography>
      </Box>);
  }
  return (
    <Modal open={true}>
      <Box sx={style}>
        <Typography id="modal-modal-title" variant="h6" component="h2">
          <b>{props.title}</b>
        </Typography>
        <form onSubmit={handleSubmit}>
        <Typography id="modal-modal-description" sx={{ mt: 2, mb: 2 }}>
          <input
            id="email"
            type="email" 
            name="email"
            placeholder="Email (ex: sara@gmail.com)" 
            style={{width:"80%", borderRadius: "1rem", padding: ".5rem 1rem"}}
            required
          />
          <ValidationError 
            prefix="Email" 
            field="email"
            errors={state.errors}
          />
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2, mb: 1 }}>
          <textarea
            id="message"
            name="message"
            rows="7"
            defaultValue={props.message} 
            style={{width:"80%", resize: "none", borderRadius: "1rem", padding: ".5rem 1rem"}}
            required
          />
          <ValidationError 
            prefix="Message" 
            field="message"
            errors={state.errors}
          />
          </Typography>
          <Button className={props.theme} sx={btnStyle} onClick={props.closePopup}>Cancel</Button>
          <Button className={props.theme} sx={btnStyle} type="submit" disabled={state.submitting}>Submit</Button>
        </form>
      </Box>
    </Modal>
  );
}

export default FeedbackForm