import { 
  getTrainingConfirmMsg,
  getReadyConfirmMsg,
  getCorrectMsg,
  getIncorrectMsg,
  getThankMsg,
  getByeMsg,
  getStartMsg,
  getErrorMsg,
  getNewTrainingMsg,
  getQuestionCountMsg,
  getTrainingUnavailableMsg,
  getFinishMsg,
  getNotDoneMsg,
  getDoneMsg,
  READY,
  STOP,
} from "./Message";
import CryptoJS from 'crypto-js';
import {
  BOT_NAME, 
  URL, 
  MULTIPLE_CHOICE, 
  TRAINING, 
  TRAINING_INFO,
  CUSTOM_MESSAGE,
  SOURCE_MARKER,
  FEEDBACK_MARKER
} from '../home/Constants'
export const JSON5 = require('json5')

class ActionProvider {

  constructor(
    createChatBotMessage,
    setStateFunc,
    createClientMessage,
    stateRef,
    createCustomMessage,
    ...rest
  ) {
    this.createChatBotMessage = createChatBotMessage;
    this.setState = setStateFunc;
    this.createClientMessage = createClientMessage;
    this.stateRef = stateRef;
    this.createCustomMessage = createCustomMessage;
  }

  

  addChatbotMessage(messages) {
    if (Array.isArray(messages)) {
      this.setState((state) => ({
        ...state, messages: [...state.messages, ...messages],
      }));
    } else {
      this.setState((state) => ({
        ...state, messages: [...state.messages, messages],
      }));
    }
  }

  setCurrentQuestion(qCurrent) {
    this.setState((state) => ({
      ...state, qCurrent: qCurrent,
    }));
  }

  getCurrentQuestion() {
    return this.stateRef.qCurrent
  }

  getQueue() {
    return this.stateRef.qQueue
  }

  getTimerState() {
    return this.stateRef.controlStateRef().timerState
  }

  getCorrectCount() {
    return this.stateRef.controlStateRef().correctCount
  }

  getTotalCount() {
    return this.stateRef.controlStateRef().totalCount
  }

  setControlState(correctCount, totalCount,timerState) {
    this.stateRef.setControlState(correctCount, totalCount, timerState)
  }

  shuffleQuestions(questions) {
    for (let i = questions.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [questions[i], questions[j]] = [questions[j], questions[i]];
    }
    return questions
  }

  async fetchQuestions(training) {
    const SOURCE = `data/${CryptoJS.SHA256(training.toLowerCase() + BOT_NAME + URL)}`.substring(0, 35) + '_' + training.toLowerCase()
    var ptr = this
    await fetch(SOURCE)
      .then(response => response.text())
      .then(text => JSON5.parse(JSON.parse(CryptoJS.AES.decrypt(text, `${training.toLowerCase()}${BOT_NAME}${URL}`).toString(CryptoJS.enc.Utf8))))
      .then(jsonArray => ptr.shuffleQuestions(jsonArray))
      .then(questions => {
        ptr.setState((state) => ({
        ...state, qQueue: questions,
        }))
        ptr.qCount = questions.length
      })
  }

  async setTraining(training) {
    this.setState((state) => ({
      ...state, training: training,
    }));
    var trainingCode = TRAINING_INFO[training] ? TRAINING_INFO[training]["code"] : ''
    if (trainingCode) {
      await this.fetchQuestions(trainingCode)
      this.setControlState(0, 0, 'reset')
      this.addMultipleMessages([
        getTrainingConfirmMsg(training), '',
        getQuestionCountMsg(this.qCount), '', 
        getStartMsg(), 'ready'
      ])
    } else {
      this.addMultipleMessages([
        getTrainingUnavailableMsg(training), '', 
        getNewTrainingMsg(), 'trainingSelection'
      ])
    }
  }


  showQuestion() {
    // process question
    var question = this.getQueue().pop()
    this.setCurrentQuestion(question)
    //update control box 
    var timerState = this.getTimerState()
    var totalCount = this.getTotalCount()
    if (timerState !== 'timing') {
      timerState = 'timing'
    }
    this.setControlState(-1, totalCount + 1, timerState)
    // if multiple choice with 4 options
    if (question["cat"] === 'mc4') {
      // shuffle choices
      var choices = ["A", "B", "C", "D"]
      this.shuffleQuestions(choices)
      var fixSolution = true
      choices.forEach((choice, index) => {
        if (fixSolution && question['s'] === choice) {
          fixSolution = false
          question['s'] = String.fromCharCode(65 + index)
        }
        question[choice] = question[choice].replace(choice + ". ", String.fromCharCode(65 + index) + ". ")
      })

      this.addMultipleMessages([
        question["q"] + SOURCE_MARKER + question["o"], '', 
        question[choices[0]] + (question[choices[0]].endsWith(".") ? "" : "."), '', 
        question[choices[1]] + (question[choices[1]].endsWith(".") ? "" : "."), '',
        question[choices[2]] + (question[choices[2]].endsWith(".") ? "" : "."), '',
        question[choices[3]] + (question[choices[3]].endsWith(".") ? "" : "."), "multipleChoice",
      ])
    }
  }

  checkAnswer(answer) {
    const current = this.getCurrentQuestion()
    if (current["s"] === answer) {
      //update control box 
      var correctCount = this.getCorrectCount()
      this.setControlState(correctCount + 1, -1, 0)
      this.addMultipleMessages([
        getCorrectMsg(), '', 
        current["_id"] + FEEDBACK_MARKER + current["detail"] + (current["detail"].endsWith(".") ? "" : "."), '', 
        getReadyConfirmMsg(), 'ready'
      ])
    } else {
      this.addMultipleMessages([
        getIncorrectMsg(), '', 
        current["_id"] + FEEDBACK_MARKER + current["detail"] + (current["detail"].endsWith(".") ? "" : "."), '', 
        getReadyConfirmMsg(), 'ready'
      ])
    }
    this.setCurrentQuestion('')
  }

  addMultipleMessages(msgArray) {
    const processedMsg = []
    for (var i = 0; i < msgArray.length; i += 2) {
      const msg = msgArray[i] 
      const widget = msgArray[i+1] 
      processedMsg.push(this.createChatBotMessage(msg, CUSTOM_MESSAGE(widget)))
    }
    this.addChatbotMessage(processedMsg)
  }

  createUserReply(text) {
    if (!text || !text.trim()) {
      return
    }
    try {
      var sanitizedText = text.trim()
      const message = this.createClientMessage(sanitizedText)
      this.addChatbotMessage(message)
      this.handleUserReply(sanitizedText)
    } catch (error) {
      console.error(error);
      const message = this.createChatbotMessage(getErrorMsg(), CUSTOM_MESSAGE())
      this.addChatbotMessage(message)
    }
  }

  handleUserReply(msg) {
    if (TRAINING.includes(msg)) {
      return this.setTraining(msg)
    }
    if (STOP.includes(msg)) {
      this.setControlState(-1, -1, 'stopped')
      return this.addMultipleMessages([
        getThankMsg(), '', 
        getByeMsg(), ''
      ])
    }
    const currentQuestion = this.getCurrentQuestion()
    if (!currentQuestion && !this.getQueue().length) {
      this.setControlState(-1, -1, 'stopped')
      return this.addMultipleMessages([
        getFinishMsg(), '',
        getNewTrainingMsg(), "trainingSelection"])
    }
    if (READY.includes(msg)) {
      if (currentQuestion) {
        return this.addMultipleMessages([getNotDoneMsg(), ''])
      }
      return this.showQuestion()
    }
    if (MULTIPLE_CHOICE.includes(msg)) {
      if (!currentQuestion) {
        return this.addMultipleMessages([getDoneMsg(), ''])
      }
      return this.checkAnswer(msg)
    }
  }
}

export default ActionProvider