export const pickOne = (array) => {
    return array[Math.floor(Math.random() * array.length)]
}

const timeGreeting = () => {
    var time = (new Date()).getHours();
    if (4 < time && time < 12) {
        return ["Good morning", "Morning", "Rise and shine", "Wakey, wakey, eggs and bakey"]
    }
    if (12 <= time && time < 18) {
        return ["Good afternoon", "Afternoon", "Lovely afternoon", "Cheers to a great afternoon"]
    }
    return ["Good evening", "Evening", "Lovely evening"]
}

export const GREETING = {
    part1: [
        "Hello",
        "Howdy",
        "Hi",
        "Greetings",
        "Look alive",
        "Good day"
    ].concat(timeGreeting()),
    part2: [
        "this is",
        "I am",
        "my name is",
        "it's",
    ],
    part3: [
        "nice to meet you",
        "welcome",
        "pleasure to meet you",
        "hope your day is going well"
    ],
}

